<template>
    <!-- 选择显示的位置 -->
    <u-popup round="40rpx" :show="show" @close="close" @open="open">
        <view>
            <view class="box">
                <view class="box-title">谁可以看</view>
                <view style="height: 10rpx;"></view>
                    <!-- 选择显示省份/直辖市，根据ovoUserSelf.location的值 -->
                    <view class="select-box-2" 
                    v-for="(item, index) in bodyData"
                    @click="select(item)"
                    >
                        <view class="select-box-item">
                            <image class="select-box-item-icon" :src="item.imgSrc"></image>
                            <view class="select-box-item-title">{{ item.text }}</view>
                            <image
                            v-if="result.code == item.code"
                            class="select-box-item-icon2"
                            :src="$kt.file.byPath('image/icon/answer.svg')"
                            ></image>
                        </view>
                    </view>


            </view>
        </view>
    </u-popup>
</template>

<script>
export default {
    data() {
        return {
            show: false,
            navHeight: 44,
            statusBarHeight: uni.getSystemInfoSync().statusBarHeight,
            navBarHeight: 48,
            bodyData: [{
                text: "所有人可见",
                code: "all",
                imgSrc: this.$kt.file.byPath('image/icon/lockPublic.svg')
            },
            {
                text: "仅互相关注可见",
                code: "mutual",
                imgSrc: this.$kt.file.byPath('image/icon/friend.svg')

            },
            {
                text: "仅关注我的人可见",
                code: "follower",
                imgSrc: this.$kt.file.byPath('image/icon/follower.svg')

            },
            {
                text: "仅自己可见",
                code: "self",
                imgSrc: this.$kt.file.byPath('image/icon/lockPrivate.svg')

            },
            ],

            result: {
                text: "",
                code: "",
            }
        }
    },
    created() {
        this.ovoUserSelf = this.$ovo.getSelf();
    },
    mounted() {
        this.select(this.bodyData[0]);

        this.navHeight = 44;
        // #ifdef MP-WEIXIN
        this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
        // #endif
        this.navBarHeight = this.navHeight - (-this.statusBarHeight);
    },
    methods: {
        select(res) {
            this.result = res;
            this.$emit('select', res);
            this.close();
        },
        open() {
            this.show = true;
        },
        close() {
            this.show = false;
        },
        // 根据经纬度计算距离，单位为米
        getDistance(lat1, lng1, lat2, lng2) {
            const lat1Rad = lat1 * Math.PI / 180;
            const lat2Rad = lat2 * Math.PI / 180;
            const lng1Rad = lng1 * Math.PI / 180;
            const lng2Rad = lng2 * Math.PI / 180;

            const a = lat1Rad - lat2Rad;
            const b = lng1Rad - lng2Rad;

            const s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
                Math.cos(lat1Rad) * Math.cos(lat2Rad) * Math.pow(Math.sin(b / 2), 2)));

            let result = s * 6378137;
            result = Math.round(result * 10000) / 10000;
            return result;
        },
    }
}
</script>

<style lang="scss" scoped>
.box {
    padding: 30rpx 0 0 0;
    background-color: #fff;
    border-radius: 30rpx;
}

.box-title {
    font-size: 30rpx;
    text-align: center;
    color: #999;
}


.select-box-2 {
    border-bottom: 1px solid #eee;
    padding: 30rpx 0 30rpx 0;


    .select-box-item {
        padding: 0 30rpx 0 30rpx;

        .select-box-item-icon{
            width: 30rpx;
            height: 30rpx;
            margin-right: 30rpx;
        }

        .select-box-item-title {
            vertical-align: top;
            display: inline-block;
            font-size: 30rpx;
            color: #333;
            margin-bottom: 10rpx;
        }

        .select-box-item-text {
            font-size: 28rpx;
            color: #999;
        }

        .select-box-item-icon2 {
            width: 30rpx;
            height: 30rpx;
            margin-right: 30rpx;
            float: right;
        }
    }
}

.select-box-2:active {
    opacity: .7;
}
</style>
